$img-size: 200px;

*{
  box-sizing: border-box;
}

body,
article,
section {
  margin: 0;
  padding: 0;
}

img {
  width: $img-size;
  height: $img-size;
  border-radius: 200px;
  margin: 0;
  transition-duration: 1s;
}

.people {
display: grid;
grid-template-columns: repeat(1, $img-size);
padding-top: 30px;
justify-content: center;
align-items: center;
column-gap: 30px;

}

@media(min-width: 500px) {
  .people {
grid-template-columns: repeat(2, $img-size);
}
}

@media(min-width: 750px) {
  .people {
grid-template-columns: repeat(2, $img-size);
}
}

@media(min-width: 1000px) {
  .people {
grid-template-columns: repeat(4, $img-size);
}
}

@media(min-width: 1500px) {
  .people {
grid-template-columns: repeat(4, $img-size);
}
}

.person {
  display: grid;
  justify-content: center;
  align-items: center;
  background: #EEEEEE;

  transition-duration: 1s;

&:hover {
    transform: scale(1.2);
  }
  
  &:hover img {
    border-radius: 60px;
}

    &:hover .extra {
    opacity: 1;
}
}

.photo {
  display: grid;
  align-items: center;
  justify-content: center;
  &:hover .extra {
    display: contents;
  }
  }

  .rank {
    width: 32px;
    height: 32px;
    margin-right: 10px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    line-height: 18.4px;
    text-align: center;
    color: #8B0000;
    background: #FFFFFF;
    align-items: center;
    border-radius: 200px;
    margin-right: 10px;
    }

  .extra {
    opacity: 0;
    font-family: Arial;
    font-size: 21.6px;
    text-align: center;
  }

    .name {
    font-family: Arial;
    font-size: 24px;
    text-align: center;
  }
  
.name {
font-family: Arial, Helvetica, sans-serif;
font-size: 20px;
line-height: 23px;
text-align: center;
}





